import TypeError from '.TypeError';
import WeakSet from '.WeakSet';
import WeakMap from '.WeakMap';
import set_has from '.WeakSet.prototype.has';
import set_add from '.WeakSet.prototype.add';
import set_del from '.WeakSet.prototype.delete';
import map_has from '.WeakMap.prototype.has';
import map_get from '.WeakMap.prototype.get';
import map_set from '.WeakMap.prototype.set';
import map_del from '.WeakMap.prototype.delete';
import isArray from '.Array.isArray';

const INLINES = new WeakMap                                                     ();
const SECTIONS = new WeakSet                ();

const deInline = /*#__PURE__*/map_del.bind(INLINES)                                                  ;
const deSection = /*#__PURE__*/set_del.bind(SECTIONS)                                                  ;

export const isInline = /*#__PURE__*/map_has.bind(INLINES)                                                  ;
export const ofInline = /*#__PURE__*/map_get.bind(INLINES)                                                      ;
export const beInline = /*#__PURE__*/map_set.bind(INLINES)                                                                                                        ;
export const inline =                                                         (value   )    => {
	beInline(value, true);
	isArray(value) || deSection(value);
	return value;
};
export const multilineTable =                                  (value   )    => {
	beInline(value, false);
	deSection(value);
	return value;
};

export const isSection = /*#__PURE__*/set_has.bind(SECTIONS)                                                                  ;
export const beSection = /*#__PURE__*/set_add.bind(SECTIONS)                                                 ;
export const Section =                            (table   )    => {
	if ( isArray(table) ) { throw TypeError(`array can not be section, maybe you want to use it on the tables in it`); }
	beSection(table);
	deInline(table);
	return table;
};

                                                       