import WeakSet from '.WeakSet';
import set_has from '.WeakSet.prototype.has';
import set_add from '.WeakSet.prototype.add';

const LITERAL = new WeakSet;

export const isLiteral = /*#__PURE__*/set_has.bind(LITERAL)                                                                    ;

export const beLiteral = /*#__PURE__*/set_add.bind(LITERAL)                                                   ;

export const literal = (literal                               , ...chars          )                   => {
	if ( typeof literal!=='string' ) {
		let index = chars.length;
		if ( index ) {
			const { raw } = literal;
			literal = raw[index] ;
			while ( index ) { chars[--index] += raw[index] ; }
			literal = chars.join('') + literal;
		}
		else { literal = literal.raw[0] ; }
	}
	const lines = literal.split('\n')                           ;
	beLiteral(lines);
	return lines;
};

                                            